.AlphaButtons {
  border: 2px solid maroon;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 600;
  cursor: pointer;
  width: 3rem;
  margin: 5px 3px 4px;
  outline: none;
  background-color: #fce728;
  -webkit-box-shadow: 0 3px #006400ab;
          box-shadow: 0 3px #006400ab;
  border-radius: 1rem;
  padding-top: 4px;
}

.AlphaButtons:not(:disabled):hover {
  background-color: #080808;
  color: whitesmoke;
}

.AlphaButtons:disabled {
  opacity: 0.5;
}
.AlphaButtons:disabled:hover {
  cursor: not-allowed;
}



